<template>
  <div class="swiper-container swiper-container-big">
    <div class="swiper-wrapper">

      <div id="cover-page" class="swiper-slide">
        <img id="part1-1" class="ani" swiper-animate-effect="bounceInDown" src="../../assets/images/summary2016/1-1.png">
        <img id="part1-2" class="ani" swiper-animate-effect="bounceInRight" swiper-animate-delay="0.5s" src="../../assets/images/summary2016/1-2.png">
        <img id="part1-3" class="ani" swiper-animate-effect="fadeIn" swiper-animate-delay="1.5s"  src="../../assets/images/summary2016/1-3.png">
        <img id="part1-4" class="ani next-page" swiper-animate-effect="bounceInUp" swiper-animate-delay="2s" :style='{display:style.displayHand}' src="../../assets/images/summary2016/1-4new.png">
      </div>

      <div id="part1" class="swiper-slide" v-if="user.RegDate<'2017.01.01'">
        <div class="title"></div>

        <div id="reg-info" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.RegDate}}</div>
          <div></div>
          <div class="last"> <span class="red">{{user.RegOrder}}</span> </div>
        </div>

        <div id="first-watch" v-if="style.displayFirstWatch" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstWatch.Time}}</div>
          <div><span class="red"></div>
          <div class="last">
            <span>PS: </span>
            <div class="img-bar">
              <img :src="user.FirstWatch.UserImg" class="avatar">
              <span class="nickname">{{user.FirstWatch.UserNickName.length>6?user.FirstWatch.UserNickName.substr(0, 6)+"...":user.FirstWatch.UserNickName}}</span>
            </div>
            <span></span>
          </div>
        </div>

        <div id="first-follow" v-if="style.displayFirstFollow" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstFollow.Time}}</div>
          <div><span class="red"></div>
          <div>TA</div>
          <div class="img img-bar-wrap">
            <img class="img-bar-bg" src="../../assets/images/summary2016/2-3.png">
            <img :src="user.FirstFollow.UserImg" class="avatar">
            <span class="nickname">{{user.FirstFollow.UserNickName.length>6?user.FirstFollow.UserNickName.substr(0, 6)+"...":user.FirstFollow.UserNickName}}</span>
          </div>
        </div>

        <div id="first-send-gift" v-if="style.displayFirstSendGift" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstSendGift.Time}}</div>
          <div><span class="red"></div>
          <div></div>
          <div class="img img-bar-wrap">
            <img class="img-bar-bg" src="../../assets/images/summary2016/2-4.png">
            <img :src="user.FirstSendGift.UserImg" class="avatar">
            <span class="nickname">{{user.FirstSendGift.UserNickName.length>6?user.FirstSendGift.UserNickName.substr(0, 6)+"...":user.FirstSendGift.UserNickName}}</span>
          </div>
        </div>

        <div id="first-lvb" v-if="user.FirstLvb" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstLvb.Time}}</div>
          <div><span class="red"></div>
          <div class="last"></div>
        </div>

        <div id="first-fan" v-if="user.FirstFans" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstFans.Time}}</div>
          <div><span class="red"></div>
          <div class="margin-100"></div>
          <section class="ani img gray-bar-wrap" swiper-animate-effect="tada">
            <img :src="user.FirstFans.UserImg" class="avatar avatar-center">
            <div class="orange"></div>
            <div class="purple">{{user.FirstFans.UserNickName.length>6?user.FirstFans.UserNickName.substr(0, 6)+"...":user.FirstFans.UserNickName}}</div>
          </section>
        </div>

        <div id="first-gift" v-if="user.FirstGift" class="module ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstGift.Time}}</div>
          <div><span class="red"></span></div>
          <div></div>
          <section class="ani img gray-bar-wrap" swiper-animate-effect="tada">
            <img :src="user.FirstGift.UserImg" class="avatar">
            <div><span class="orange">{{user.FirstGift.UserNickName.length>6?user.FirstGift.UserNickName.substr(0, 6)+"...":user.FirstGift.UserNickName}}</span></div>
            <img class="fighting" src="../../assets/images/summary2016/fighting.png">
          </section>
        </div>
      </div>

      <div id="part2" class="swiper-slide" v-if="user.RegDate<'2017.01.01'">
        <div class="title"></div>

        <div id="lvb-record" class="module ani" v-if="user.LvbRecord && !user.HotLive" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.LvbRecord.Time}}</div>
          <div><span class="red">{{user.LvbRecord.Mins}}</span></div>
          <div class="last"></div>
          <section class="ani img gray-bar-wrap flexbox" swiper-animate-effect="tada">
            <img class="clock" src="../../assets/images/summary2016/clock.png">
            <span>{{user.LvbRecord.Mins}}</span>
            <img class="max" src="../../assets/images/summary2016/max.png">
          </section>
        </div>

        <div id="first-op-sex-fan" v-if="user.FirstOpSexFans && !user.HotLive && !user.LvbRecord" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.FirstOpSexFans.Time}}</div>
          <div><span class="red"></div>
          <div></div>
          <section class="ani img gray-bar-wrap last" swiper-animate-effect="tada">
            <img :src="user.FirstOpSexFans.UserImg" class="avatar avatar-center">
            <div class="orange"><span class="purple">{{user.FirstOpSexFans.UserNickName.length>6?user.FirstOpSexFans.UserNickName.substr(0, 6)+"...":user.FirstOpSexFans.UserNickName}}</span></div>
            <img class="heart animated pulse infinite" src="../../assets/images/summary2016/heart.png">
          </section>
        </div>

        <div id="hot-live" v-if="user.HotLive" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.HotLive.Time}}</div>
          <div></div>
          <div><span class="orange">{{user.HotLive.UserNickName.length>6?user.HotLive.UserNickName.substr(0, 6)+"...":user.HotLive.UserNickName}}</span></div>
          <div class="ani hot-live-bar last" swiper-animate-effect="lightSpeedIn" swiper-animate-delay="1s">
            <img :src="user.HotLive.UserImg" class="avatar">
            <div class="nickname">
              <div>{{user.HotLive.UserNickName.length>9?user.HotLive.UserNickName.substr(0, 9)+"...":user.HotLive.UserNickName}}</div>
              <div></div>
              <div class="ani gift" swiper-animate-effect="rubberBand" swiper-animate-delay="3s">
                <img class="kiss" src="../../assets/images/summary2016/kiss.png">
                <span class="orange">x99</span>
              </div>
            </div>
          </div>
        </div>
        
        <div id="sum1" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">2016</div>
          <div v-if="user.InteractInfo.InteractCount>0"> <span class="red">{{user.InteractInfo.InteractCount}}</span> </div>

          <div v-if="user.WatchLvbInfo.WatchCount>=5"><span class="red">{{user.WatchLvbInfo.WatchCount}}</span></div>
          <div v-if="user.WatchLvbInfo.CommentCount>=50"><span class="red">{{user.WatchLvbInfo.CommentCount}}</span></div>
          <div class="last" v-if="user.WatchLvbInfo.MostLikeUser">
            <div class="img-bar">
              <img :src="user.WatchLvbInfo.MostLikeImg" class="avatar">
              <span class="nickname">{{user.WatchLvbInfo.MostLikeUser.length>6?user.WatchLvbInfo.MostLikeUser.substr(0, 6)+"...":user.WatchLvbInfo.MostLikeUser}}</span>
            </div>
          </div>
          
          <div v-if="user.LvbInfo">
            <div v-if="user.LvbInfo.LvbCount>=5"><span class="red">{{user.LvbInfo.LvbCount}}</span></div>
            <div v-if="user.LvbInfo.GiftCount>=100"><span class="red">{{user.LvbInfo.GiftCount}}</span></div>
            <div class="last" v-if="user.LvbInfo.Myangel">
              <div class="img-bar">
                <img :src="user.LvbInfo.Myangel.UserImg" class="avatar">
                <span class="nickname">{{user.LvbInfo.Myangel.UserNickName.length>6?user.LvbInfo.Myangel.UserNickName.substr(0, 6)+"...":user.LvbInfo.Myangel.UserNickName}}</span>
              </div>
            </div>
          </div>
        </div>

        <div id="part2-none" class="ani" v-if="style.displayPart2" swiper-animate-effect="fadeInDown">
          <div></div>
          <div>,</div>
          <div></div>
          <div>^o^)/~</div>
        </div>
      </div>

      <div id="part3" class="swiper-slide" v-if="user.RegDate<'2017.01.01'">
        <div class="title"> </div>

        <div id="anchor-info" v-if="user.AnchorInfo && style.displayAnchorInfo" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.AnchorInfo.Time}}</div>
          <div></div>
          <div class="last"> <span class="red">{{user.AnchorInfo.Rank}}</span> {{user.AnchorInfo.Type}}</div>
        </div>

        <div id="add-v-info" v-if="user.AddVinfo && style.displayAddVinfo" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.AddVinfo.Time}}</div>
          <div>V
            <div class="img-bar">
              <img :src="user.HeadImg" class="avatar">
              <img src="../../assets/images/v.png" class="add-v">
              <span class="nickname">{{user.NickName}}</span>
            </div>
          </div>
          <div class="last"> <span class="red">{{user.AddVinfo.Rank}}</span> V</div>
        </div>

        <div id="act-reward" v-if="user.Reward && user.Reward.Rank<=200 && user.Reward.Rank!=0" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.Reward.Time}}</div>
          <div class="last">  <span class="red">{{user.Reward.Rank}}</span> </div>
          <img class="success-man ani" swiper-animate-effect="rotateIn" swiper-animate-delay="1s" src="../../assets/images/summary2016/success-man.png">
        </div>

        <div id="act-red-package" v-if="user.RedPackage && style.displayRedPackage" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">{{user.RedPackage.Time}}</div>
          <div class="last">  <span class="red">{{user.RedPackage.Count}}</span> </div>
          <img class="success-man ani" swiper-animate-effect="rotateIn" swiper-animate-delay="1s" src="../../assets/images/summary2016/success-man.png">
        </div>

        
        <div id="sum2" class="ani" swiper-animate-effect="fadeInDown">
          <div class="date">2016</div>
          <div id="level-info" v-if="user.LevelInfo.Level>=30">
            <div> <span class="red">{{user.LevelInfo.Level}}</span>   <span class="red">{{user.LevelInfo.LevelCompare}}%</span> </div>
          </div>
          <div v-if="user.InteractInfo && style.displayInteractInfo">
            <div v-if="user.InteractInfo.LikeMeCount>=50"><span class="red">{{user.InteractInfo.LikeMeCount}}</span></div>
            <div v-if="user.InteractInfo.ShareMeCount>=30"><span class="red">{{user.InteractInfo.ShareMeCount}}</span></div>
            <div v-if="user.InteractInfo.CommentMeCount>=100"><span class="red">{{user.InteractInfo.CommentMeCount}}</span></div>
          </div>

          <div v-if="user.FansCount && style.displayFansCount">
            <div class="first"><span class="red">{{user.FansCount}}</span></div>
            <div class="last"></div>
          </div>

          <div v-if="user.Rewordsummary">
            <div v-if="user.Rewordsummary.Count>0"><span class="red">{{user.Rewordsummary.Count}}</span></div>
            <div class="last" v-if="user.Rewordsummary.Vir>0"><span class="red">{{user.Rewordsummary.Vir}}</span> </div>
          </div>

          <div><span v-if="user.MyCities.length>0" class="red">{{user.MyCities.length}}</span><span v-else class="red">1</span></div>
          <div v-if="user.MyCities.length>0" class="last"><span class="red">{{user.MyCities[0]}}</span></div>
          <div v-else class="last"></div>
          <img class="heart animated pulse infinite" src="../../assets/images/summary2016/heart.png">
        </div>

        <img v-if="IS_EASTMONEY" class="share-btn animated pulse infinite" @click="openShare" src="../../assets/images/summary2016/share-btn.png">
        <img v-if="!IS_EASTMONEY" class="share-btn-wechat animated pulse infinite" @click="openShare" src="../../assets/images/summary2016/share-btn-wechat.png">
      </div>

      <div id="new-register" class="swiper-slide" v-if="user.RegDate>='2017.01.01'">
        <img class="text ani" swiper-animate-effect="fadeInDown" swiper-animate-duration="1.5s" src="../../assets/images/summary2016/new-register.png" >
        <img class="man"src="../../assets/images/summary2016/man.png">
      </div>

      <div id="bye-info" class="swiper-slide">
        <div>2017</div>
        <div></div>
        <div></div>
        <a class="last animated pulse infinite" @click="go2RedPack">&gt;&gt;</a>

        <img class="bye ani" swiper-animate-effect="tada" swiper-animate-delay="1s"src="../../assets/images/summary2016/bye.png">
        <img class="man"src="../../assets/images/summary2016/man.png">
      </div>
    </div>
    <div class="swiper-pagination"></div>
    <div class="loading" v-if="loading">
      <div class="black"></div>
      <img src="../../assets/images/summary2016/loading.png">
      <div>...</div>
    </div>
    <img class="qrcode" v-if="!IS_EASTMONEY" @click="downLoadApp" src="../../assets/images/summary2016/qrcode.png" >
  </div>

</template>

<script>
  import 'swiper'
  import 'swiper/dist/css/swiper.min.css'
  // import 'swiper-animate'
  // import 'swiper-animate/animate.min.css'
  import * as http from '../../utils/common'
  import { IS_WEIXIN, IS_EASTMONEY, IS_LIVE } from '../../utils/useragent'
  import { SET_TITLE, SHOW_SHARE } from '../../utils/emapp'

  const downLoadUrl = 'http://lvbqas.eastmoney.com/openApp.html?type=home'

  let Uid

  export default {
    data () {
      return {
        user: {
          Uid: null,
          FirstWatch: {},
          FirstFollow: {},
          FirstSendGift: {},
          FirstLvb: {},
          FirstFans: {},
          FirstGift: {},
          LvbRecord: {},
          FirstOpSexFans: {},
          HotLive: {},
          AnchorInfo: {},
          AddVinfo: {},
          Reward: {},
          RedPackage: {},
          Regdays: 0,
          LevelInfo: {},
          InteractInfo: {},
          WatchLvbInfo: {},
          LvbInfo: {
            Myangel: {}
          },
          Rewordsummary: {},
          MyCities: []
        },
        page: {
          p: 0,
          ps: 10
        },
        count: 0,
        loading: true,
        style: {
          displayHand: 'none',
          displayFirstSendGift: false,
          displayFirstWatch: false,
          displayFirstFollow: false,
          displayHotLive: false,
          displayLvbRecord: false,
          displayFirstOpSexFans: false,
          displayPart2: false,
          displayAddVinfo: false,
          displayRedPackage: false,
          displayAnchorInfo: false,
          displayFansCount: false,
          displayInteractCount: false,
          displayPart3: false
        },
        IS_EASTMONEY: IS_EASTMONEY
      }
    },
    methods: {
      getUserInfo () {
        let _this = this
        _this.loading = true
        let url
        if (IS_EASTMONEY) {
          url = '/MyStory/Index'
        } else {
          url = '/MyStory/Index/share?uid=' + Uid
        }
        http.post({
          url: url,
          succ (json) {
            // console.log(JSON.stringify(json))
            // console.log(json)
            if (json) {
              // console.log(JSON.stringify(_this.user))
              _this.filterData(json)
            }
          }
        })
      },
      filterData (json) {
        let _this = this
        // 1 
        let countPart1 = 1
        if (json.FirstLvb) {
          countPart1++
        }
        if (json.FirstGift) {
          countPart1++
        }
        if (json.FirstFans) {
          countPart1++
        }
        if (json.FirstSendGift && countPart1 < 3) {
          _this.style.displayFirstSendGift = true
          countPart1++
        }
        if (json.FirstWatch && countPart1 < 3) {
          _this.style.displayFirstWatch = true
          countPart1++
        }
        if (json.FirstFollow && countPart1 < 3) {
          _this.style.displayFirstFollow = true
        }

        // 3 
        let countPart3 = 0
        if (json.LevelInfo.Level >= 30) {
          countPart3++
        }
        if (json.Reward) {
          if (json.Reward.Rank <= 200 && json.Reward.Rank !== 0) {
            countPart3++
          }
        }
        if (json.Rewordsummary) {
          if (json.Rewordsummary.Count > 0) {
            countPart3++
          }
        }
        if (json.AddVinfo && countPart3 < 4) {
          _this.style.displayAddVinfo = true
          countPart3++
        }
        if (json.RedPackage && countPart3 < 4) {
          _this.style.displayRedPackage = true
          countPart3++
        }
        if (json.AnchorInfo && countPart3 < 4) {
          _this.style.displayAnchorInfo = true
          countPart3++
        }
        if (json.FansCount >= 20 && countPart3 < 4) {
          _this.style.displayFansCount = true
          countPart3++
        }
        if (json.InteractInfo && countPart3 < 4) {
          _this.style.displayInteractInfo = true
          countPart3++
        }
        if (countPart3 === 0) {
          _this.style.displayPart3 = true
        }

        _this.user = json
        // _this.initSwiper()
        setTimeout(() => _this.initSwiper(), 1000)
        _this.style.displayHand = 'block'
      },
      initSwiper () {
        let _this = this
        _this.loading = false
        var swiper = new Swiper('.swiper-container-big', {
          direction: 'vertical',
          pagination: '.swiper-pagination',
          paginationClickable: true,
          keyboardControl: true,
          nextButton: '.next-page',
          onInit: function (swiper) {
            swiperAnimateCache(swiper)
            swiperAnimate(swiper)
          },
          onSlideChangeEnd: function (swiper) {
            swiperAnimate(swiper)
          },
          onTransitionEnd: function (swiper) {
            swiperAnimate(swiper)
          }
        })
        console.log(swiper)
        _this.share()
      },
      downLoadApp () {
        if (!IS_LIVE) {
          _czc.push(['_trackEvent', '2016', 'APP'])
          window.location = downLoadUrl
        }
      },
      go2RedPack () {
        _czc.push(['_trackEvent', '2016', ''])
        if (IS_EASTMONEY || IS_WEIXIN) {
          window.location = 'http://lvbhd.eastmoney.com/frontend/single/#!/act/redpack4/1?push=1'
        } else {
          this.$router.push({path: '/act/go2RedPack'})
        }
      },
      share () {
        let _this = this
        /*  */
        if (IS_EASTMONEY) {
          setTimeout(() => SET_TITLE('2016'), 200)
          setTimeout(() => SHOW_SHARE('1'), 10)
        }

        /*  */
        if (IS_WEIXIN) {
          window.wxShare({
            title: '2016',
            desc: '1617',
            link: window.location.href,
            imgUrl: window.location.origin + window.location.pathname + 'static/images/share_summary2016.png'
          })
        }
        /* APP */
        if (IS_EASTMONEY) {
          window.emH5ShareInfo = () => {
            _czc.push(['_trackEvent', '2016', '()'])
            var shareInfo = {
              img: window.location.origin + window.location.pathname + 'static/images/share_summary2016.png',
              title: '2016',
              url: window.location.origin + window.location.pathname + '#/act/summary2016?uid=' + _this.user.Uid,
              desc: '1617'
            }
            var jsonStr = '{"callbackname":"callbackShare","type": "qq,weixin,pengyouquan,weibo","title":"' + shareInfo.title + '","img":"' + shareInfo.img + '","url":"' + shareInfo.url + '", "desc": "' + shareInfo.desc + '"}'
            if (/(iPhone|iPod|iPad|iTouch|iOS)/i.test(navigator.userAgent)) {
              window.location = 'onwebshareclicked:' + jsonStr
            } else {
              window.eastmoney.onShareClicked(jsonStr)
            }
          }
        }
      },
      openShare () {
        if (IS_EASTMONEY) {
          window.emH5ShareInfo()
        } else {
          window.location = downLoadUrl
        }
      }
    },
    created () {
      let _this = this
      _this.getUserInfo()
    },
    mounted () {
      // let _this = this
      // setTimeout(() => _this.initSwiper(), 1000)
      // _this.initSwiper()
    },
    beforeRouteEnter (to, from, next) {
      // let _this = this
      // console.log(next)
      Uid = to.query.uid
      console.log(Uid)
      next()
    },
    route: {
      data ({ to }) {
        let _this = this
        _this.userid = parseInt(to.params.userid)
        // _this.getUserInfo()
        // _this.share()

        // 
        window.onpageshow = function (event) {
          if (event.persisted) {
            window.location.reload()
          }
        }
      }
    }
  }
</script>

<style lang='less' src='../../assets/less/summary2016.less'></style>


